  border-radius: 10%;
}

.intro-message {
    margin-top: 2%;
    font-size: 1.5em;
    width : 55%;
}

.Header {
    margin-bottom: 5%;
    text-align: center;
}

.intro-short {
    text-align: center;
}


.Work-Logo {
  width: 15%; 
  height: 15%;
  padding : 1%;
}

.Work-Title {
  font-weight: bold;
}

#Work > .row {
  font-size: 1.2em;
  padding : 15px;
}

#posting {
    font-size: 3em;
}

#post-view {
    /* padding : 10vw */
}

#writing-box {
    width : 80%;
}

.post a:hover {
    color: red
}

.post a {
    color : white
}

.post {
    margin: 0.5vw
}

.post img{
    width : 15vw;
    height: 15vw
}

.timestamp {
    font-size: 1em;
}

#blog-pagination {
    /* position: absolute;
    top: 85vh; */
}


#navigationBar {
    background:#12232e;;
    position: fixed;
    min-width: 100vw;
    padding-top: 1vh;
    padding-bottom: 1vh;
    z-index: 2;
}

.navigation-item a {
    color: #fff;
    font-weight: bold;
}
  
  /* Color changes when hover */
.navigation-item a:hover {
    color: #49cb6e;
    text-decoration: none;
}

.navigation-item {
    padding-left: 8.5vw;
    padding-right: 8.5vw;
}
.login-box {
    text-align: center;
    /* background: rgb(61, 150, 209); */
}

.signin-box {
    text-align: center;
    /* background: rgb(61, 150, 209); */
}
.contact-icon {
    width : 5vw
}

.footer {
    background-color:rgb(146, 98, 98);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
